svg{
  height: 85vh;
  width: 100%;
  path{
    stroke: rgb(108, 169, 226);
    fill: transparent;
    transition: 0.5s;
    cursor: pointer;
    &:hover{
    fill: rgb(255, 194, 52);
    stroke: #aaa;
    transform: translate(-5px,-5px);
    }
  }
  @media(max-width:992px){
    width: 60%;
  }
  @media(max-width:768px){
    width: 100%;
    height: 100%;
  }
}

